.app {
    font-family: "Comic Sans MS", sans-serif;
    background: linear-gradient(180deg, #E6D4DE 0%, #9890C7 100%);
    color: #2D2E46;
    height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
}

.linear-progress {
    margin-top: 60px;
}

.packs {
    width: 100%;
    display: flex;
    padding: 10px;
    overflow-y: auto;
}
